"use client"

import type React from "react"

import { useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/contexts/AuthContext"
import { usePermission } from "@/contexts/PermissionContext"
import { Loader2, ShieldAlert } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"

interface ProtectedRouteProps {
  children: React.ReactNode
  requiredPermission?: string
  requiredPermissions?: string[]
  requireAll?: boolean
  fallback?: React.ReactNode
}

export default function ProtectedRoute({
  children,
  requiredPermission,
  requiredPermissions = [],
  requireAll = false,
  fallback,
}: ProtectedRouteProps) {
  const { isAuthenticated, loading: authLoading } = useAuth()
  const { hasPermission, hasAllPermissions, hasAnyPermission, loading: permissionLoading } = usePermission()
  const router = useRouter()

  // Verificar autenticação
  useEffect(() => {
    if (!authLoading && !isAuthenticated) {
      router.push("/login")
    }
  }, [isAuthenticated, authLoading, router])

  // Verificar se o usuário está autenticado e se as permissões estão carregadas
  const loading = authLoading || permissionLoading

  // Se estiver carregando, mostrar loader
  if (loading) {
    return (
      <div className="flex h-screen w-full items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  // Se não estiver autenticado, não renderizar nada (o redirecionamento acontecerá no useEffect)
  if (!isAuthenticated) {
    return null
  }

  // Verificar permissões
  const hasAccess = () => {
    // Se não houver permissões requeridas, permitir acesso
    if (!requiredPermission && (!requiredPermissions || requiredPermissions.length === 0)) {
      return true
    }

    // Verificar permissão única
    if (requiredPermission && !hasPermission(requiredPermission)) {
      return false
    }

    // Verificar múltiplas permissões
    if (requiredPermissions && requiredPermissions.length > 0) {
      if (requireAll && !hasAllPermissions(requiredPermissions)) {
        return false
      }

      if (!requireAll && !hasAnyPermission(requiredPermissions)) {
        return false
      }
    }

    return true
  }

  // Se o usuário não tiver acesso
  if (!hasAccess()) {
    // Se houver um fallback, mostrar
    if (fallback) {
      return <>{fallback}</>
    }

    // Caso contrário, mostrar mensagem de acesso negado
    return (
      <div className="flex h-screen w-full flex-col items-center justify-center p-4">
        <Alert variant="destructive" className="max-w-md">
          <ShieldAlert className="h-5 w-5" />
          <AlertDescription className="mt-2">Você não tem permissão para acessar esta página.</AlertDescription>
        </Alert>
      </div>
    )
  }

  // Se passar nas verificações, renderizar o conteúdo
  return <>{children}</>
}
